import Swiper from 'swiper';
import { FreeMode, Mousewheel, Parallax } from 'swiper/modules';

function portfolioSlider() {
	const info = document.querySelector('.portfolio__info')
	const about = document.querySelector('.portfolio__about')
	const width = window.innerWidth

	document.addEventListener('DOMContentLoaded', () => {
		console.log(width)
		if (width > 768){
			console.log('Слайдер работает')
			const swiper = new Swiper('.portfolio__slider', {
				modules: [FreeMode, Mousewheel, Parallax],
				direction: 'horizontal',
				freeMode: true,
				parallax: true,
				mousewheel: {
					sensitivity: 6
				},
				centeredSlides: true,
				slidesPerView: 2.5,
				spaceBetween: 60,
			})

			swiper.on('slideChange', function () {
				console.log('Слайды меняются');
				swiper.activeIndex > 0 ? info.classList.add('hide') : info.classList.remove('hide')
				// swiper.isEnd == true ? links.classList.remove('hide') : links.classList.add('hide')
				swiper.activeIndex >= 5 ? about.classList.remove('hide') : about.classList.add('hide')
				// links.classList.add('hide')
			});
		} else {
			about.classList.remove('hide')
		}
	})

	// swiper.on('reachBeginning', function () {
	// 	console.log('Начало слайдера');
	// 	about.classList.toggle('hide')
	// });

	// swiper.on('reachEnd', function () {
	// 	console.log('Слайды закончались');
	// 	links.classList.toggle('hide')
	// });


}

